.object-slide-card {
    position: relative;
    z-index: 0;
    padding: 150px 32px 32px;
    margin-top: -67px;
    width: calc(100% - 50px);
    margin-left: 32px;
    box-shadow: 0 5px 30px 0 rgba(25, 53, 117, 0.1);
    background-color: #fff;

    &::before {
        position: absolute;
        content: '';
        left: -32px;
        top: 45px;
        width: 32px;
        height: 80%;
        background-color: #f5f7f9;
    }
}


.object-slide-card__title {
    margin-bottom: 5px;

    font-size: 16px;
    font-weight: 700;
}

.object-slide-card__address {
    margin-bottom: 65px;

    font-size: 14px;
    font-weight: 400;
}

.object-slide-card__data {
    font-size: 14px;
    line-height: 24px;
    font-weight: 400;
    color: rgba($darktext, 0.6);

    span {
        display: block;
    }

    b {
        font-weight: 700;
    }
}

.object-slide-card__data-content {
    margin: 0;
    margin-bottom: 30px;

    &:last-child {
        margin-bottom: 0;
    }
}
